/***/ (function(module, exports) {

	module.exports = {"data":{"post":{"html":"<p>Cem just pointed me to the blog <a href=\"http://usaerklaert.wordpress.com/\">USA Erklärt</a> which attempts to explain in German certain things about the USA. When I saw the entry on <a href=\"http://usaerklaert.wordpress.com/2006/09/18/warum-amerikaner-briten-kanadier-nicht-sagen-was-sie-meinen/\">Why Americans Don't Say What They Really Mean</a>, I was struck by the passage</p>\n<blockquote><p>\n  For example, it is considered crude to say \"no\" directly, therefore something else will be said that every Anglo-Saxon will understand as \"no\", but does not use the word \"no\".\n</p></blockquote>\n<p>I was bitten by this a couple of weeks ago. A couple days before the May Day <a href=\"http://www.papascott.de/archives/2007/04/28/bridge-days/\">bridge day</a>, MamaMaus was on the phone with her sister-in-law and they thought it would be a great idea to invite their kids to visit Christopher that day, even though I had to work. I didn't think of much of MamaMaus volunteering my time as a babysitter, and would have much rather taken Christopher to the park or a movie or something, so I told her \"no\" as directly as I could:</p>\n<blockquote><p>\n  I really don't know what I would do with them.\n</p></blockquote>\n<p>This was of course interpreted as \"he would love for them to come\", and thus I was reminded (once again) that to say no to a German you pretty much have to use a two by four with applied centrifugal force. The kids came on Monday, they were bored, I was bored, pretty much as expected. Next time I'll try to remember to be more direct.</p>","fields":{"title":"The Problem With No","date":"16 May 2007","url":"https://papascott.de/2007-05-16-the-problem-with-no/"}}},"pathContext":{"slug":"/2007-05-16-the-problem-with-no/","prev":{"fields":{"title":"im in ur serverz","date":"16 May 2007","slug":"/2007-05-16-im-in-ur-serverz/"}},"next":{"fields":{"title":"Rocket Revisited","date":"17 May 2007","slug":"/2007-05-17-rocket-revisited/"}}}}

/***/ })

